

:root {
    background-color: rgb(244, 244, 244);
}

[theme="blue"] {
    --theme: #2eb2d2;
    --theme-darker: #148ba8;
}

[theme="green"] {
    --theme: #0ea765;
    --theme-darker: #0a7245;
}

[theme="red"] {
    --theme: #fc7455;
    --theme-darker: #de4328;
}

[theme="black"] {
    --theme: #353535;
    --theme-darker: rgb(33, 31, 31);
}

.card-container {
    display: flex;
    align-items: center;
    margin: 2px;
    column-gap: 1px;
    text-align: center;
}
.card:hover {
    cursor: pointer;
}
.card > * {
    padding: 1.5em 2em;
}

.card__title {
    font-size: 1.3em;
    background-color: var(--theme-darker);
    color: rgb(237, 237, 223);
}

.card__price {
    background-color: var(--theme);
    color: rgb(237, 237, 223);
}

.price-span {
    font-size: 2em;
}

.card__speed {
    color: gray;
    background-color: rgb(247, 247, 254);
}

.card__desc {
    background-color: #e3e3e3;
    color: gray;
}

.card_selected {
    box-shadow: rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset,
        rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset,
        rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset,
        rgba(0, 0, 0, 0.06) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px,
        rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px,
        rgba(0, 0, 0, 0.09) 0px 32px 16px;
}

